@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
.card {
	background: white;
	width: 100%;
	border-radius: 0.4rem;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
	display: -webkit-flex;
	display: flex;
	padding: 1.3rem;
}

.filterTab {
	max-width: var(--desktop-width);
	position: relative;
	margin-bottom: 1rem;
	min-height: 4.3rem;
	transition: opacity 300ms linear;
}

.filterTab--show {
	opacity: 1;
}

.filterTab--hide {
	opacity: 0;
}

.filterTab__container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	grid-gap: 1rem;
	gap: 1rem;
}

.filterTab__tags {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	grid-gap: 0.8rem;
	gap: 0.8rem;
}

.filterTab__clear {
	cursor: pointer;
	font-weight: 600;
	color: var(--DarkGrayishCyan);
}

.filterTab__clear:hover {
	color: var(--DarkCyan);
	text-decoration: underline;
}

.tag {
	background: var(--LightGrayishCyan);
	border-radius: 0.3rem;
	color: var(--DarkCyan);
	font-weight: 600;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	cursor: default;
}

.tag__name {
	margin: auto 0.5rem;
	font-size: 0.9rem;
}

.tag__close {
	width: 1.7rem;
	height: 1.7rem;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	background: var(--DarkCyan);
	color: var(--LightGrayishCyan);
	cursor: pointer;
	border-radius: 0px 0.3rem 0.3rem 0px;
}

.tag__close:hover {
	background-color: var(--VeryDarkGrayishCyan);
}

.job__filterTag {
	font-size: 0.9rem;
	font-weight: 600;
	padding: 0.5rem;
	background-color: var(--LightGrayishCyan);
	color: var(--DarkCyan);
	border-radius: 5px;
	cursor: pointer;
	line-height: 1rem;
}

.job__filterTag:hover {
	color: var(--LightGrayishCyan);
	background-color: var(--DarkCyan);
}

.job {
  position: relative;
}

.job--featured {
  border-left: 0.4rem solid var(--DarkCyan);
  border-radius: 0.7rem;
}

.job__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  grid-gap: 2rem;
  gap: 2rem;
}

.job__logo {
  width: 88px;
  height: 88px;
}

.job__description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 0.3rem;
  gap: 0.3rem;
}

.job__tags {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.job__enterprise {
  color: var(--DarkCyan);
  font-size: 0.9rem;
}

.job__chip {
  padding: 0 0.4rem;
  border-radius: 0.9rem;
  font-weight: 600;
  text-align: center;
  font-size: 0.6rem;
  color: white;
  line-height: 23px;
}

.job__chip--new {
  background-color: var(--DarkCyan);
}
.job__chip--featured {
  background-color: var(--VeryDarkGrayishCyan);
}

.job__title {
  color: var(--VeryDarkGrayishCyan);
  font-size: 1.1rem;
  cursor: pointer;
}

.job__title:hover {
  color: var(--DarkCyan);
}

.job__info {
  color: var(--DarkGrayishCyan);
  font-size: 0.9rem;
}

.job__filterTags {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  margin-left: auto;
  max-width: 50%;
}

@media only screen and (max-width: 580px) {
  .job__container {
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .job__logo {
    position: absolute;
    left: 1.3rem;
    top: -22px;
    width: 44px;
    height: 44px;
  }

  .job__description {
    margin-top: 1rem;
    border-bottom: 1px solid var(--VeryDarkGrayishCyan);
    padding-bottom: 1rem;
    width: 100%;
  }

  .job__filterTags {
    margin-left: 0;
    max-width: unset;
  }
}

.header {
	height: 10rem;
	background-image: url(/static-job-listing-react/static/media/bg-header-desktop.8694f466.svg);
	background-repeat: no-repeat;
	background-color: var(--DarkCyan);
	background-size: cover;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: relative;
}

@media only screen and (max-width: 580px) {
	.header {
		background-image: url(/static-job-listing-react/static/media/bg-header-mobile.9cb13612.svg);
	}
}

:root {
  --LightGrayishCyan: hsl(180, 52%, 96%);
  --DarkGrayishCyan: hsl(180, 8%, 52%);
  --VeryDarkGrayishCyan: hsl(180, 14%, 20%);
  --DarkCyan: hsl(180, 29%, 50%);

  --desktop-width: 1440px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 15px;
  font-family: "Spartan", sans-serif;
}

body {
  background-color: hsl(180, 52%, 96%);
  background-color: var(--LightGrayishCyan);
}

main {
  position: relative;
  top: -2.15rem;
  display: -webkit-flex;
  display: flex;
}

.container {
  max-width: 1440px;
  width: 70%;
  margin: 0 auto;
  grid-gap: 2rem;
  gap: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

@media only screen and (max-width: 890px) {
  .container {
    width: 90%;
    grid-gap: 2.5rem;
    gap: 2.5rem;
  }
}

